body {
    margin: 0;
    padding: 0;
    font-family: "Open Sans", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#nav.ant-menu-horizontal {
    border-bottom: none;
}

#nav {
    border: 0;
}

#nav.ant-menu-horizontal > .ant-menu-item > a {
    color: #fff;
    line-height: 43px;
}

#nav.ant-menu-horizontal > .ant-menu-item > a:hover {
    color: #fff;
}

#nav.ant-menu-horizontal > .ant-menu-item:hover,
#nav.ant-menu-horizontal > .ant-menu-item-selected {
    border-bottom: 2px solid #ffffff;
    color: #ffffff;
}

#nav.ant-menu-horizontal > .ant-menu-item-selected a {
    font-weight: bold;
}

#header {
    background: #07cb79;
    box-shadow: 0 2px 8px #f0f1f2;
    z-index: 10;
    max-width: 100%;
    width: 100%;
    height: 45px;
    line-height: 45px;
}

#header .header-inner {
    display: flex;
    justify-content: flex-end;
}

#sidebar {
    background: #fff;
    z-index: 99;
    box-shadow: 2px 0 0 #f0f1f2;
    height: 100vh;
}

#content {
    padding: 24px 0;
}

.container {
    width: 100%;
    max-width: 960px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 auto;
}

.sidecontent {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.ppcard {
    margin-bottom: 50px;
    background: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .09);
    border-color: rgba(0, 0, 0, .09);
}

.ppimage {
    width: 100%;
}

.profile {
    padding: 20px;
}

.profile-info {
    color: #3d4451;
    padding-bottom: 25px;
    margin-bottom: 25px;
    border-bottom: 1px solid #dedede;
}

.ppcard-inner {
    padding: 57px 50px 15px 50px;
}

.profile-preword {
    margin-bottom: 28px;
}

.profile-preword span {
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.1;
    display: inline-block;
    padding: 7px 12px;
    text-transform: uppercase;
    position: relative;
    background-color: #07cb79;
}

.profile-preword span:before {
    content: '';
    width: 0;
    height: 0;
    top: 100%;
    left: 5px;
    display: block;
    position: absolute;
    border-style: solid;
    border-width: 0 0 8px 8px;
    border-color: transparent;
    border-left-color: #07cb79;
}

.profile-title {
    font-size: 36px;
    line-height: 1.1;
    font-weight: 700;
    margin-bottom: 5px;
}

.profile-title span {
    font-weight: 300;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
    font-family: "Open Sans", sans-serif;
    margin: 0 0 25px 0;
    line-height: 1.1;
}

.profile-position {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.1;
    margin-bottom: 0;
}

.profile-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.profile-list li {
    margin-bottom: 13px;
}

.profile-list .title {
    display: block;
    width: 120px;
    float: left;
    color: #333333;
    font-size: 12px;
    font-weight: 700;
    line-height: 20px;
    text-transform: uppercase;
}

.profile-list .cont {
    display: block;
    margin-left: 125px;
    font-size: 15px;
    font-weight: 400;
    line-height: 20px;
    color: #9da0a7;
}

.profile-list .button {
    color: #fff;
    font-size: 12px;
    font-weight: 700;
    line-height: 1;
    text-transform: none;
    padding: 5px 8px;
    display: inline-block;
    position: relative;
    top: -2px;
    border-radius: 3px;
    background-color: #07cb79;
}

a {
    color: #07cb79;
}

.social {
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: center;
    background: #07cb79;
}

.social li {
    display: inline-block;
    margin: 5px 15px;
}

.social li a {
    width: 45px;
    height: 45px;
    position: relative;
    background-color: transparent;
    -webkit-transition: -webkit-transition, background-color 0.25s linear 0s;
    -webkit-transition: background-color 0.25s linear 0s, -webkit-transition;
    transition: background-color 0.25s linear 0s, -webkit-transition;
    transition: transition, background-color 0.25s linear 0s;
    transition: transition, background-color 0.25s linear 0s, -webkit-transition;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 50%;
    display: table-cell;
    vertical-align: middle;
}

.social li a:hover {
    text-decoration: none;
    background-color: rgba(0, 0, 0, 0.1);
}

/*.social li a, .social li a .rsicon {*/
    /*line-height: 45;*/
/*}*/

.social li a .rsicon {
    font-size: 25px;
}


.skils {
    padding: 40px 30px;
    margin-bottom: 50px;
    background: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .09);
    border-color: rgba(0, 0, 0, .09);
}


.progress-bar {
    position: relative;
    margin-bottom: 40px;
    padding: 0 10px;
}

.progress-bar .bar-data {
    font-size: 14px;
    line-height: 1.1;
    padding-right: 40px;
}

.progress-bar .bar-value {
    font-size: 16px;
    position: absolute;
    right: 0;
    top: 0;
    display: block;
}

.progress-bar .bar-title {
    display: block;
    margin-bottom: 5px;
}

.progress-bar .bar-fill {
    width: 0;
    height: 100%;
    display: block;
    position: relative;
    z-index: 1;
    border-radius: 5px;
    -webkit-transition: width 400ms ease-out 100ms;
    transition: width 400ms ease-out 100ms;
    background-color: #07cb79;
}

.progress-bar .bar-line {
    height: 5px;
    position: relative;
    background-color: #07cb7936;
}

.progress-bar .bar-line:after {
    content: '';
    opacity: 0.2;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    border-radius: 5px;
}

@media (min-width: 768px) {
    .section-skills .section-box .row:last-child .progress-bar {
        margin-bottom: 0;
    }
}

@media (max-width: 767px) {
    .section-skills .section-box {
        padding-bottom: 35px;
    }

    .section-skills .section-box .row:last-child .col-sm-6:last-child .progress-bar {
        margin-bottom: 0;
    }
}

.section-title {
    color: #3d4451;
    font-size: 34px;
    line-height: 1.2;
    font-weight: 600;
    text-align: center;
}

.about {
    font-size: 20px;
    line-height: 1.8;
}

#footer {
    text-align: center;
}

.clearfix {
    zoom: 1;
}
